











import Vue from "vue";
import MenuNav from "@/components/MenuNav.vue";
import {
    Component
} from "vue-property-decorator";
@Component({
    components: {
        MenuNav,
    },
})
export default class EditLabel extends Vue {
    get clictHeight() {
        const clict = document.documentElement.getBoundingClientRect();
        return (clict.height || 1000) + "px";
    }
}
